/* HeroSection.css */
.hero {
    color: #fff;
    padding: 4rem 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
}

h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
}

p {
    font-size: 1.25rem;
    margin-bottom: 2rem;
}

.hero-buttons {
    display: flex;
    justify-content: center;
    gap: 1rem;
}

.btn-primary {
    background-color: #1976d2; /* Primary color */
    color: #fff;
    padding: 0.75rem 1.5rem;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
    transition: background-color 0.3s;
}

.btn-primary:hover {
    background-color: #155a9a; /* Darker shade of primary color */
}

.btn-secondary {
    background-color: #ff9800; /* Accent color */
    color: #fff;
    padding: 0.75rem 1.5rem;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1rem;
    font-weight: bold;
    transition: background-color 0.3s;
}

.btn-secondary:hover {
    background-color: #e68900; /* Darker shade of accent color */
}

/* Responsive Design */
@media (max-width: 768px) {
    .hero {
        padding: 2rem 1rem;
    }

    h1 {
        font-size: 2.5rem;
    }

    p {
        font-size: 1rem;
    }
}
