/* ContactPage.css */
.contact-page {
    background-color: #f5f5f5; /* Light background color for the page */
}

.contact-hero {
    background-color: #ffffff; /* White background for the hero section */
    padding: 40px 0;
}

.contact-hero h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
}

.contact-hero p {
    font-size: 1.2rem;
    color: #555;
}

.contact-form-section {
    background-color: #ffffff; /* White background for the form section */
    padding: 40px 0;
}

.contact-form-section h2 {
    font-size: 2rem;
    margin-bottom: 20px;
}

.contact-page .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

/* Example styling for responsiveness */
@media (max-width: 768px) {
    .contact-hero h1 {
        font-size: 2rem;
    }

    .contact-form-section h2 {
        font-size: 1.5rem;
    }
}

