/* AboutUsPage.css */
.about-us-page {
    background-color: #f5f5f5; /* Light background for the page */
}

.about-us-hero {
    background-color: #ffffff; /* White background for the hero section */
    padding: 40px 0;
}

.about-us-hero h1 {
    font-size: 2.5rem;
    margin-bottom: 10px;
}

.about-us-hero p {
    font-size: 1.2rem;
    color: #555;
}

.our-mission {
    background-color: #ffffff; /* White background for the mission section */
    padding: 40px 0;
}

.our-mission h2 {
    font-size: 2rem;
}

.team {
    background-color: #ffffff; /* White background for the team section */
    padding: 40px 0;
}

.team-members {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.team-member {
    flex: 1 1 calc(50% - 20px); /* Two columns layout */
    text-align: center;
}

.team-member img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    margin-bottom: 10px;
}

.team-member h3 {
    font-size: 1.5rem;
    margin: 10px 0;
}

.team-member p {
    font-size: 1rem;
    color: #777;
}

/* Example styling for responsiveness */
@media (max-width: 768px) {
    .team-member {
        flex: 1 1 100%; /* Single column layout on small screens */
    }
}

