.cta-section {
    padding: 40px;
    background-color: #1976d2;
    color: #ffffff;
    text-align: center;
    border-radius: 8px;
}

.cta-section h2 {
    font-size: 2em;
    margin-bottom: 20px;
}

.cta-section button {
    background-color: #ff9800;
    border: none;
    color: #ffffff;
    padding: 15px 25px;
    font-size: 1em;
    border-radius: 5px;
    cursor: pointer;
}
