/* HomePage.css */

/* Container for the home page */
.home-page {
    padding: 20px;
    background-color: #ffffff;
}

/* Hero section */
.hero-section {
    background-color: #1976d2;
    color: #ffffff;
    padding: 50px 20px;
    text-align: center;
}

.hero-section h1 {
    font-size: 3em;
}

.hero-section p {
    font-size: 1.5em;
    margin-top: 20px;
}

/* CTA section */
.cta-section {
    background-color: #8bc34a;
    color: #ffffff;
    padding: 30px 20px;
    text-align: center;
    margin-top: 20px;
}

.cta-section h2 {
    font-size: 2em;
}

.cta-section p {
    font-size: 1.2em;
    margin-top: 10px;
}

.cta-section button {
    background-color: #ffffff;
    color: #1976d2;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    font-size: 1em;
    cursor: pointer;
    margin-top: 15px;
}

.cta-section button:hover {
    background-color: #f5f5f5;
}
