.why-choose-us {
    padding: 40px;
    background-color: #f5f5f5;
}

.why-choose-us .text {
    text-align: center;
    margin-bottom: 30px;
}

.why-choose-us h2 {
    color: #1976d2;
    margin-bottom: 15px;
}

.why-choose-us .features {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.feature {
    text-align: center;
    margin: 15px;
    flex: 1 1 30%;
    max-width: 300px;
}

.feature-icon {
    width: 50px;
    height: 50px;
    margin-bottom: 10px;
}

.feature h3 {
    color: #333;
    margin: 10px 0;
}

.feature p {
    color: #666;
}

