.services-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

.services-hero {
    background-color: #f5f5f5;
    padding: 40px 0;
    text-align: center;
}

.services-hero h1 {
    color: #1976d2;
    margin-bottom: 15px;
}

.services-hero p {
    color: #333;
    font-size: 1.2em;
}

.services-overview-section {
    padding: 40px 0;
}

.cta-section {
    background-color: #1976d2;
    color: #fff;
    padding: 40px 0;
    text-align: center;
}

.cta-section .cta-button {
    background-color: #ff9800;
    color: #fff;
    padding: 10px 20px;
    text-decoration: none;
    border-radius: 5px;
    font-size: 1.1em;
    margin-top: 20px;
}

.cta-section .cta-button:hover {
    background-color: #e68900;
}
