.contact-form {
    padding: 20px;
    background-color: #f5f5f5;
    border-radius: 8px;
    max-width: 600px;
    margin: auto;
  }
  
  .contact-form h2 {
    margin-bottom: 20px;
    font-size: 2em;
    color: #333;
  }
  
  .contact-form form {
    display: flex;
    flex-direction: column;
  }
  
  .contact-form input,
  .contact-form textarea {
    margin-bottom: 15px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1em;
  }
  
  .contact-form button {
    padding: 10px;
    background-color: #1976d2;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1em;
  }
  
  .contact-form button:disabled {
    background-color: #aaa;
  }
  
  .contact-form .form-status {
    margin-top: 10px;
    font-size: 1em;
    color: #333;
  }
  